import React, { Component } from 'react';
import { NavLink } from 'react-router-dom'
import '../App.css';

class Header extends Component {
  render() {
    const logo = '/images/logoExp.png';
    const udemyLogo = '/images/udemyLogo.png';
    const bannerUdemy = '/images/BannerUdemy.jpg';
    const twitter = "https://twitter.com/tntcproject";
    const instagram = "https://www.instagram.com/tntcproject/";
    const facebook = "https://www.facebook.com/toughnutocrack/";
    const udemy = "https://www.udemy.com/game-development-professionale-con-unity-3d-e-c/";
    const youtube = "https://www.youtube.com/channel/UCtj_LuYF9i5xkk4Q9EYamYA";
    const youtubeVideo = "https://www.youtube.com/embed/hTHy2L2bqCs";
    const github = "https://github.com/ToughNutToCrack";

    return (
        <div>
          <div className="row">
            <div className="col"></div>
            <div className="col-6">
              <img src={logo} className="img-fluid" alt="Responsive image"/>
            </div>
            <div className="col"></div>
          </div>

          <ul className="nav justify-content-center navigation">
            <li className="nav-item">
              <NavLink className="nav-link" activeClassName="active" to="/Home">Home</NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" activeClassName="active" to="/AboutUs">About Us</NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" activeClassName="active" to="/Blog">DevBlogs</NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link disabled-link disabled" activeClassName="active" to="/Projects">Our Projects</NavLink>
            </li>
          </ul>
        </div>
    );
  }
}

export default Header;



