import React, { Component } from 'react';
import { BrowserRouter, Switch, NavLink } from 'react-router-dom'
import { IndexRoute, Router, Route } from 'react-router';
import ReactMarkdown from 'react-markdown'
import Article from './Article';

import '../App.css';

class Blog extends Component {
  render() {

    const webpackRequireContext = require.context('../_posts', false, /.md$/);
    const blogs = webpackRequireContext.keys().reduce((memo, fileName) => memo.set(fileName.match(/.\/([^.]+).*/)[1], webpackRequireContext(fileName)), new Map());

    const blogIndex = (blogs) => <ul className="nav flex-column ">{[...blogs.keys()].map(path => <li className="nav-item" key={path}><NavLink className="nav-link" activeClassName="active" to={'/Blog/'+path}>{blogs.get(path).title || path} </NavLink></li>)}</ul>;

    console.log( this.props.match);
    return (
        <div className="row p-3">

          <div className="col-lg-2 articlesIndex"> {blogIndex(blogs)} </div>
          <div className="col articleBody">
            <Route exact path="/Blog/:name" component={Article}/>
          </div>
        </div>
    );
  }
}



export default Blog;



