import React, { Component } from 'react';
import '../App.css';

class Udemy extends Component {
  render() {
    const logo = '/images/logoExp.png';
    const udemyLogo = '/images/udemyLogo.png';
    const bannerUdemy = '/images/BannerUdemy.jpg';
    const udemy = "https://www.udemy.com/game-development-professionale-con-unity-3d-e-c/";
    const youtubeVideo = "https://www.youtube.com/embed/hTHy2L2bqCs";

    return (
      <div className="row udemy-banner p-3">
        <div className="col-0 col-lg-6 align-self-center ">
          <a href={udemy}>
            <img src={udemyLogo} className="img-fluid" />
          </a>
        </div>
        <div className="col-12 col-lg-6 align-self-center">
          Our course is available on <a href={udemy}><span>Udemy</span></a>.

          <div className="embed-responsive embed-responsive-16by9">
            <iframe className="embed-responsive-item" src={youtubeVideo}
              frameborder="0" allow="autoplay; encrypted-media" allowfullscreen>
            </iframe>
          </div>
        </div>
      </div>
    );
  }
}

export default Udemy;



