import React, { Component } from 'react';
import '../App.css';

class AboutUs extends Component {
  render() {
    const mattImg = '/images/matt.jpg';
    const erikImg = '/images/erik.jpg';

    const erikTwitter = 'https://twitter.com/doJester13';
    const erikLinkedin = 'https://www.linkedin.com/in/erik-minarini-228945b4/';
    const erikMail = 'mailto:minarini.work@gmail.com';

    const mattTwitter = 'https://twitter.com/_MatteoBaraldi_';
    const mattLinkedin = 'https://www.linkedin.com/in/matteo-baraldi-51747b103';
    const mattMail = 'mailto:matteo.baraldi91@gmail.com ';

    return (
      <div>
        <div className="row p-3">
          <div className="col about p-3 text-center">
            <p> 
                TNTC was born to share our great passion for development of videogames and software. <br/>
                Follow us through our blog s articles and videos published weekly <br/>
                Our first Game Development course in italian is available on Udemy. <br/>
            </p>
          </div>
        </div>

        <div className="card-deck p-3">
            <div className="card" >
              <div className="card-body text-center">
                <img alt="ErikImg" className="rounded-circle" src={erikImg} />
                <h5 className="card-title">Erik Minarini</h5>
                <p className="card-text">{"Hi. I'm Erik, nice to meet you."}</p>
                <p className="card-text"><span>{"I'm passionate about everything is related to IT."}</span>
                <span>{"In particular I like graphic and 3D development."}</span>
                <span>{"I had studied in University of Bologna where I also started working with Android, another passion of mine."}</span>
                <span>{"I've always loved videogames, since childhood."}</span>
                <span>{"Today I'm a Unity Developer and this allows me to create both applications and games."}</span>
                <span>{"I've founded ThoughNutToCrack to share knowledge and passion with everyone."}</span>
                </p>
              </div>
              <div className = "card-footer text-center">
                <div className="card-contact">
                  <a href={erikTwitter}><i className="fa fa-twitter fa-2x"></i></a>
                  <a href={erikLinkedin}><i className="fa fa-linkedin fa-2x"></i></a>
                  <a href={erikMail}><i className="fa fa-envelope fa-2x"></i></a>
                </div>
              </div>
            </div>
            <div className="card">
              <div className="card-body text-center">
                <img alt="MatteoImg" className="rounded-circle" src={mattImg}/>
                <h5 className="card-title">Matteo Baraldi</h5>
                <p className="card-text">{"Hello everyone, my name is Matteo."}</p>
                <p className="card-text">
                  <span>{"I love everything that surrounds the develop of apps, from the programming to the artistic part."}</span>
                  <span>{"I work as Unity Developer creating apps of all sorts and forms and in my spare time i enjoy develop videogames with my team."}</span>
                  <span>{"ThoughNutToCrack has the intention to share this passion to other people."}</span>
                </p>
              </div>
              <div className = "card-footer text-center">
                <div className="card-contact">
                  <a href={mattTwitter}><i className="fa fa-twitter fa-2x"></i></a>
                  <a href={mattLinkedin}><i className="fa fa-linkedin fa-2x"></i></a>
                  <a href={mattMail}><i className="fa fa-envelope fa-2x"></i></a>
                </div>
              </div>
            </div>
        </div>
      </div>
    );
  }
}

export default AboutUs;



