import React, { Component } from 'react';
import '../App.css';

class SocialBar extends Component {
  render() {
    const twitter = "https://twitter.com/tntcproject";
    const instagram = "https://www.instagram.com/tntcproject/";
    const facebook = "https://www.facebook.com/toughnutocrack/";
    const udemy = "https://www.udemy.com/game-development-professionale-con-unity-3d-e-c/";
    const youtube = "https://www.youtube.com/channel/UCtj_LuYF9i5xkk4Q9EYamYA";
    const github = "https://github.com/ToughNutToCrack";
    const udemyLogo = "/images/udemy.png";
      
    return (
      <socialbar>
        <div className="row">
          <div className="col align-self-center">
            <a href={twitter}><i className="fa fa-twitter fa-2x"></i></a>
            <a href={instagram}><i className="fa fa-instagram fa-2x"></i></a>
            <a href={facebook}><i className="fa fa-facebook fa-2x"></i></a>
            <a href={youtube}><i className="fa fa-youtube fa-2x"></i></a>
            <a href={github}><i className="fa fa-github fa-2x"></i></a>
            <a href={udemy}><img src={udemyLogo} className="socialbar-udemyLogo"/></a>

          </div>
        </div>
      </socialbar>
    );
  }
}

export default SocialBar;



