import React, { Component } from 'react';
import '../App.css';

class SubscribePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        emailValue: '',
        fNameValue: '',
        lNameValue: '',
    };
  }

    render() {
        return (
            <div className="row">
              <div className="col subscribeForm p-3">
                <form action="https://github.us19.list-manage.com/subscribe/post" method="POST" noValidate>
                <input type="hidden" name="u" value="992108a64fda641468c6da3e2"/>
                <input type="hidden" name="id" value="0401b4f172"/>
                  <div className="form-group row p-3">
                    <div className="col-12 align-content-center"><a>Keep in touch with us, subscribe our newsletter :)</a></div>
                    <div className="col-4 offset-4 subscribeField">
                      <input
                          className = "form-control-plaintext whiteText"
                          type="email"
                          name="EMAIL"
                          id="MERGE0"
                          value={this.state.emailValue}
                          onChange={ (e)=>{this.setState({emailValue: e.target.value});} }
                          autoCapitalize="off"
                          autoCorrect="off"
                          placeholder="Email"
                       />
                       <span className="underline"></span>
                       <span className="underlineAnimation"></span>

                     </div>
                  </div>

                    <input type="submit" value="Subscribe" name="subscribe" id="mc-embedded-subscribe" className="button btn btn-dark btn-lg"/>
                    <div style={{position: 'absolute', left: '-5000px'}} aria-hidden='true' aria-label="Please leave the following three fields empty">
                    <label htmlFor="b_name">Name: </label>
                    <input type="text" name="b_name" tabIndex="-1" value="" placeholder="Freddie" id="b_name"/>

                    <label htmlFor="b_email">Email: </label>
                    <input type="email" name="b_email" tabIndex="-1" value="" placeholder="youremail@gmail.com" id="b_email"/>

                    <label htmlFor="b_comment">Comment: </label>
                    <textarea name="b_comment" tabIndex="-1" placeholder="Please comment" id="b_comment"></textarea>
                </div>
              </form>
            </div>
          </div>
        )
    }
}

export default SubscribePage



